img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  margin-top: 5px;
  margin-bottom: 20px;
}

.responsive {
  width: 100%;
  height: auto;
}

.carousel-inner img {
  width: 100%;
  height: 100%;
}

footer {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px 0px;
  color: #6781c7;
}

.btn {
  margin: 10px 0px 10px 0px;
}

.btn-group {
  margin: 10px 0px 10px 0px;
}

.attention-text {
  color: #c96b30;
}

.forgot-password {
  font-size: 12px;
  color: #43e6a7;
  text-decoration: none;
}

.signin-btn {
  border-radius: 15px;
  padding-inline-start: 30px;
  padding-inline-end: 30px;
}

p {
  margin-bottom: 0.8em;
}

.up-arrow {
  font-size: larger;
  text-decoration: none;
  color: #34bf75;
}

.up-arrow :hover {
  text-decoration: none;
  color: #24d4a8;
}

::placeholder {
  color: teal !important;
  opacity: 0.3 !important;
}

.resumeeText {
  background-color: white;
  color: rgb(38, 27, 133);
  border-radius: 5px;
  margin-top: 30px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
