@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
body {
  background: linear-gradient(-45deg, #14a1ff, #b3dffc, #4ab6ff, #65aff0);
  background-size: 2000% 2000%;
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
  font-family: Roboto, sans-serif;
  margin: 0;
  height: 100vh;
  display: grid;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.header {
    max-width: 400px;
    height: 250px;
}

.content {
    width: 94%;
    margin: 4em auto;
    font-size: 20px;
    line-height: 30px;
    text-align: justify;
}


nav {
    position: flex;
    width: 100%;
    text-decoration: none;
    /* line-height: 60px; */
}

nav ul {
    /* line-height: 60px; */
    list-style: none;
    /* background: rgba(0, 0, 0, 0); */
    overflow: hidden;
    color: #fff;
    padding: 0;
    text-align: center;
    margin: 0;
    /* padding-right: 40px; */
    transition: 1s;
}

/* nav.black ul {
    background: #000;
} */

nav ul li {
    display: inline-block;
    text-decoration: none;
    padding: 16px 40px;
}

nav ul li a {
    text-decoration: none;
    color: #fff;
    font-size: 16px;
}
nav ul li :hover {
    color:#4c29e9;
    text-decoration: none;
}
 
.active {
    color: #6781c7;
    text-decoration: none;
}


.menu-icon {
    /* line-height: 60px; */
    width: 100%;
    background: #000;
    text-align: right;
    box-sizing: border-box;
    padding: 15px 24px;
    cursor: pointer;
    color: #fff;
    display: none;
}


